.address-Popover-warp .address-list {
  margin-bottom: 10px;
}
.address-Popover-warp .address-list.line {
  border-bottom: 1px solid #d1d1d1;
  padding-bottom: 10px;
}
.address-Popover-warp .address-list.Source-search-box {
  display: flex;
  height: 38px;
}
.address-Popover-warp .address-list.Source-search-box .ant-input-search {
  width: 300px;
  margin-right: 15px;
}
.address-Popover-warp .address-list.Source-search-box .test-Source-submit {
  background: #27CDA2;
  border-radius: 5px;
  border: none;
  padding-left: 15px;
  padding-right: 15px;
  color: #fff0f6;
}
.address-Popover-warp .address-list .ant-radio-group {
  border-bottom: 1px solid #d1d1d1;
  padding-bottom: 10px;
}
.address-Popover-warp .address-list .ant-radio-button-wrapper {
  margin: 5px;
}
.address-Popover-warp .address-list .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  color: #27CDA2;
  border-color: #27CDA2;
}
.showAnswerActive {
  color: red;
}
.blank-show-anwser {
  position: relative;
  color: red;
}
.blank-show-anwser:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: #333;
}
/*解析*/
.analyze-wrap-modal .analyze-error {
  cursor: pointer;
  color: #DE923F;
  font-size: 14px;
  margin-bottom: 0;
  text-align: right;
  position: absolute;
  right: 40px;
  top: 25px;
}
.analyze-wrap-modal .analyze-error i {
  margin-right: 5px;
}
.analyze-wrap-modal .analyze-modal-card .ant-card-head-title {
  white-space: normal;
  padding: 0 0 10px;
}
.analyze-wrap-modal .ant-card-head {
  padding: 0 15px;
}
.analyze-wrap-modal .ant-card-body {
  padding: 15px;
}
.analyze-wrap-modal .ant-card-body .testing .testing-name {
  color: #27CDA2;
}
/*纠错*/
.error-test-modal .ant-checkbox-wrapper + .ant-checkbox-wrapper {
  margin-left: 0;
}
.error-test-modal .ant-checkbox-wrapper {
  margin-right: 8px;
}
/*相似题*/
.analyze-wrap-modal .analyze-modal-group .ant-radio-button-wrapper {
  border: none;
}
.analyze-wrap-modal .analyze-modal-group .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #27CDA2;
  border-radius: 4px;
  color: #fff;
}
.analyze-wrap-modal .analyze-modal-group .ant-radio-button-wrapper:before {
  width: 0;
}
.analyze-wrap-modal .analyze-modal-change {
  cursor: pointer;
  color: #DE923F;
  font-size: 14px;
}
.analyze-wrap-modal .question-modal-container {
  align-items: center;
  font-size: 12px;
}
.analyze-wrap-modal .question-modal-container .question-modal-left {
  color: #6B706F;
}
.analyze-wrap-modal .question-modal-container .question-modal-left span {
  margin-right: 10px;
}
.analyze-wrap-modal .question-modal-container .question-modal-right {
  text-align: right;
}
.analyze-wrap-modal .question-modal-container .question-modal-right span {
  margin-left: 10px;
  cursor: pointer;
  color: #DE923F;
}
.analyze-wrap-modal .question-modal-container .question-modal-right .ant-btn span {
  color: #fff;
}
